export default {
	data() {
		return {
			currentIndex:0,
			cityList:[],		// 一级城市(省)
			city1:[],			// 二级城市
		};
	},
	mounted() {
		// 从缓存中读取一级城市
		// this.getCity1()
	},
	methods: {
		// 获取一级城市（省份）
		getCity1(){
			var city = uni.getStorageSync('city')
			this.cityList = (JSON.parse(city)).data
			console.log(this.cityList)
			if(this.cityList.length == 0){
				this.getCity('',(res)=>{
					uni.setStorage({
						key: 'city',
						data: JSON.stringify(res),
						success: function () {
							
						}
					})
				})
			}else{
				// 获取二级城市
				var id = this.cityList[0].CODE
				var obj = {
					id
				}
				this.getCity(obj,(res)=>{
					this.city1 = res.data
				})
			}
		},
		// 公用接口-获取城市列表
		getCity(getData,callback){
			uni.request({
				url: this.$lib + '/city',
				data: getData,
				method:'GET',//请求方式  或GET
				success: res => {
					callback(res.data)
				}
			});
		},
	}
};
